<template>
  <div class="container" id="login">
    <h3 class="text-center">登录</h3>
    <form role="form" class="form-horizontal">
      <div class="form-group">
        <label for="inputEmail3" class="control-label col-xs-3">用户名：</label>
        <div class="col-xs-7">
          <input v-model.lazy="username" type="email" class="form-control" placeholder="请输入用户名" />
        </div>
      </div>
      <div class="form-group">
        <label for="inputPassword3" class="control-label col-xs-3">密码：</label>
        <div class="col-xs-7">
          <input v-model.lazy="password" type="password" class="form-control" placeholder="请输入密码" />
          <p style="color:red">{{msg}}</p>
        </div>
      </div>
      <div class="form-group">
        <div class="col-xs-8 col-xs-offset-2">
          <p class="key">
            没有账户？请点击<router-link to="/register" >注册</router-link>
            <button @click="login()" type="button" class="btn btn-danger pull-right">登录</button>
          </p>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data(){
    return{
      username:"",
      password:"",
      msg:"",
    }
  },
  methods:{
    login(){
      this.axios({
        method:"post",
        url:"/prod-api/api/login",
        data:{
          "username":this.username,
          "password":this.password
        },
      })
      .then((resopnse)=>{
        if(resopnse.status == 200){
          this.username = "";
          this.password = "";
          this.msg = "";
          if(resopnse.data.code == 200){
            // let token = "eyJhbGciOiJIUzUxMiJ9.eyJsb2dpbl91c2VyX2tleSI6IjgxNDQ3OWYyLWNhOTctNDdlOC04NzdjLTRiNWM4M2I1NjcxMCJ9.RdA6HI-bccf6Y2TnFnPrTSTKG4XU4p0HRfoEPCy2NwM5Z1dtvAacqNaUcZPNS_7i5Qe21m5gQWGDs7YVddFtiA";
            // sessionStorage.setItem('Authorization',token);
            
            sessionStorage.setItem('Authorization',resopnse.data.token);
            this.$router.push('/index');
          }else{
            this.msg = resopnse.data.msg;
          }
        }
      })
      .catch((error)=>{
        console.log(error)
      })
    }
  },
  created(){
    
  }
}
</script>

